<!-- 3样式：解决样式 -->
<style scoped>
  .homeBanner {
    width: 100%;
  }

  .homePImg {
    text-align: center;
  }

  .homeMainWrap {
    margin-top: 80px;
  }

  .homeProducts {
    min-width: 500px;
    width: 35%;
    margin: 0 auto;
  }

  .homeImformationWrap {
    height: 500px;
    display: table-cell;
    vertical-align: middle;
    text-align: center;
  }

  .homeImformationWrap p {
    color: #5e5e5e;
    padding: 0 80px;
    line-height: 30px;
  }

  .homeImformationTitle {
    padding-left: 60px;
  }
</style>



